/* ----------------------- */
/* SHARE BTN CSS */
/* ----------------------- */

ul.share-buttons{
  list-style: none;
  padding: 0;
}

ul.share-buttons li{
  display: inline;
}

ul.share-buttons .sr-only{
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.avatar {
  max-width: 10% !important;
  height: auto;
}

a {
  color: #000036 !important;
}

.game-form{
  padding: 3%;
  margin: 5% 10%;
  background:#2f96a3;
  background-image: url(/speedRead-FrontEnd/static/media/otis-redding.9befbfcf.png);
  color: #000036;
}

.form-item {
  position: relative;
}

.form-item .form-tip {
  visibility: hidden;
  background-color: #008799;
  width:200px;
  padding: 2px;
  color: #dde7eb;
  border-width: 1px;
  border-color: #dde7eb;
  text-align: center;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
}

.form-item:hover .form-tip{
  visibility: visible;
  cursor: pointer;
}

.tip-image {
  margin: 3px;
  margin-right: 5px;
  height:16px;
  width:16px;
}

label {
  padding-right: 5px;
  color: #000036;
  font-weight: bold;
}


.setup-boxes {
  background-color: #dde7eb;
  color: #000036;
  font-size: 0.8em;
  border-style: none;
}

.choose_titles {
  background-color: #dde7eb;
  color: #000036;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 80px;
  scrollbar-width: thin;
  scrollbar-color: #CFD8DC #90A4AE;
}

.choose_titles::-webkit-scrollbar {
  width: 11px;
}

.choose_titles::-webkit-scrollbar-track {
  background: #90A4AE;
}

.choose_titles::-webkit-scrollbar-thumb {
  background-color: #CFD8DC;
  border: 3px solive #90A4AE
}

.title-list {
  color: #000036;
}

.title-list:hover{
  color: black;
  background-color: #90A4AE;
  cursor: pointer;
}

.practice-state{
  color: #dde7eb;
  font-size: larger;
  font-weight: bolder;
}

.reading-section{
  background-image: url(/speedRead-FrontEnd/static/media/lined-paper.e8729278.png);
  background-color: #dde7eb;
  width: 90%;
  height: 100px;
}

.easy {
