  padding-left: 20px;
  padding-right: 20px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.medium {
  font-size: x-large;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.hard {
  font-size: medium;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  padding-bottom: 20px;
}

.chart {
  margin-bottom: 20px;
  padding: 10;
}

.table-format {
  background-color: #dde7eb;
}

.chart_setup {
  height: 100px;
  width: 100px;
}

.reading-sound {
  cursor: pointer;
  line-height: 1.8;
  padding: 5px;
}

.media-control {
  padding-right: 7px;
  position: relative;
}

.media-control .tiptext {
  visibility: hidden;
  background-color: #008799;
  padding: 2px;
  color: #dde7eb;
  text-align: center;
  border-radius: 2px;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
}

.media-control:hover .tiptext{
  visibility: visible;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: 000036;
}

.App-link {
  color: #000036;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
    background-color: #dde7eb;
    color: #000036;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    border: none;
    border-radius: 6px;
    outline: none;
    transition: 0.4s;
  }
  
  /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
  .active, .accordion-item:hover {
    background-color: #ccc;
    padding: 2px;
  }
  
  /* Style the accordion panel. Note: hidden by default */
  .panel {
    padding: 0 40px 0 40px;
    background-color: #e4eaec;
    display: block;
    overflow: hidden;
    color:#000036;
    /* text-indent: 25px; */
  }

  .heading {
    font-size: 1.2em;
  }
 .react-calendar-heatmap {
    width: 100%;
    height: 200px;
 }

 .react-calendar-heatmap text {
    font-size: 10px;
    fill: #000036;
  }
  
  .react-calendar-heatmap .react-calendar-heatmap-small-text {
    font-size: 5px;
  }
  
  .react-calendar-heatmap rect:hover {
    stroke: #555;
    stroke-width: 1px;
  }
  
  /*
   * Default color scale
   */
  
  .react-calendar-heatmap .color-empty {
    fill: #dde7eb;
  }
  
  /* .react-calendar-heatmap .color-filled {
    fill: #8cc665;
  }
     */
  .react-calendar-heatmap .color-scale-0 {
    fill: #90f2ff;
  }
  .react-calendar-heatmap .color-scale-1 {
    fill: #4feaff;
  }
  .react-calendar-heatmap .color-scale-2 {
    fill: #00d5f1;
  }
  .react-calendar-heatmap .color-scale-3 {
    fill: #00a0b7;
  }
  .react-calendar-heatmap .color-scale-4 {
    fill: #005763;
  }
 


